	module.exports = {"data":{"markdownRemark":{"id":"/Users/cmarlow/Projects/lo-life/src/pages/awaken-your-authentic-self.md absPath of file >>> MarkdownRemark","html":"<h2>12 week program</h2>\n<p>This program empowers you to reconnect with the deepest part\nof your essential self. Here you will learn who you are and what\nyou need to feel purpose and fulfillment, and let go of the beliefs\nand thoughts that no longer serve you.</p>\n<p>What is included:</p>\n<ul>\n<li>Life assessment call (free assessment - 45 min)</li>\n<li>12 weekly meetings via phone, Skype or zoom</li>\n<li>Unlimited support via email, text or messenger</li>\n<li>Customized program and action plan</li>\n<li>Authentically Me SMART goals to keep momentum going</li>\n<li>Weekly homework and follow-up</li>\n<li>Breathing &#x26; Meditation tools</li>\n</ul>\n<p>By the end of this program you will:</p>\n<ul>\n<li>Connect deeper to your true inner self, &#x26; learn how you can better express and bring present your authentic self in your everyday life</li>\n<li>Allow yourself to open your heart to the universe and what is has to offer, allowing your heart to feel more peace, happiness, inspiration &#x26; desire.</li>\n<li>Have created a detailed action plan, with goals &#x26; steps to support the manifestation of your life’s vision</li>\n<li>Learn to love your authentic self, and how to make yourself the first priority, and feel a greater sense of purpose and meaning.</li>\n<li>Create a vision board, and build a daily ritual for yourself that supports the realization of your purpose.</li>\n<li>Become more in tuned with yourself, which will lead to a more excited, inspired, passionate, &#x26; fulfilled life.</li>\n<li>Build a stronger sense of self and inner power to be able to listen and follow your intuition.</li>\n</ul>","frontmatter":{"date":"May 21, 2018","path":"/awaken-your-authentic-self","title":"Awaken your Authentic self"}}},"pathContext":{}}

